html, body {
    height: 100%;
    /* compatibility */
    background: #0a92a4;
    padding: 0;
    margin: 0;

    /** mobile devices scroll */
    touch-action: none;         /* блокирует скролл/пинч поверх канваса */
    -webkit-user-select: none;  /* отключает выделение */
    user-select: none;
    -webkit-user-drag: none;
    /** /mobile devices scroll */
}

canvas {
    user-select: none;
    -webkit-user-select: none;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent;
    -moz-user-select: none;
    -ms-user-select: none;
}

#loader {
    --progress: 0;
    --width: 518px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#loader-icon {
    width: 96px;
    height: 96px;
    background: url("./images/heart.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    transform: scaleX(-1);
    margin-left: calc(-50px + var(--progress) * var(--width));

    /* Добавляем анимацию */
    animation: rocking 3s ease-in-out infinite;
    transform-origin: center bottom; /* Точка вращения - центр нижней границы */
    will-change: transform; /* Оптимизация анимации */
}

#loader-wrap {
    background: #b0cde8;
    width: var(--width);
    height: 32px;
    border-radius: 16px;
    overflow: hidden;
    border: 2px solid #00315e;
}

#loader-bar {
    background: linear-gradient(180deg, #76a0c9, #0057fd);
    width: calc(var(--progress) * 100%);
    height: 100%;
}


/* Добавляем ключевые кадры для анимации покачивания */
@keyframes rocking {
    0%, 100% {
        transform: scaleX(-1) rotate(-4deg);
    }
    25% {
        transform: scaleX(-1) rotate(3deg);
    }
    50% {
        transform: scaleX(-1) rotate(-2deg);
    }
    75% {
        transform: scaleX(-1) rotate(2deg);
    }
}


@media (max-width: 750px) {
    #loader {
        --width: 300px;
    }
}

@media (max-width: 400px) {
    #loader {
        --width: 200px;
    }
}

